
@font-face {
  font-family: 'Chinese Romantic';
  src: url('/static/downcc.ttf') format('truetype');
}
#letter {
  padding: 20px 60px;
  font-size: 40px;
  color:#fff4f4;
}
.word {
  font-family: 'Chinese Romantic';
}
.translated {
  -webkit-animation: show .5s forwards;
          animation: show .5s forwards;
}
@-webkit-keyframes show {
0% {opacity: 0
}
100% {opacity: 1
}
}
@keyframes show {
0% {opacity: 0
}
100% {opacity: 1
}
}




